.text-code {
  z-index: 10;
  font-size: 1.5rem;
  text-align: center;
  padding: 1em;
  font-weight: 500;
}

.text-code-small {
  position: relative;
  font-size: 1rem;
  padding: 1em;
  text-align: center;
}

.text-code-small:first-child {
  border-right: 1px solid rgba(255, 255, 255, 0.2);
}
