body,
body * {
  margin: unset;
  box-sizing: unset;
  padding: unset;
  color: unset;
  text-decoration: unset;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2em;
  height: 100vh;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
  letter-spacing: 0.025rem;
}
